body{
  background-color: #B22234;
}
.App {
  text-align: center;
  height: 99vh;
  overflow: hidden;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #B22234;
  height: 100%;
  padding: 20px;
  color: white;
  -ms-flex-direction: column;
      flex-direction: column;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

input {
  height: 3em;
  width: 5em;
  padding-left: 0em;
  background-color: rgba(0,0,0,0);
  border-width: 4px;
  border-style: solid;
  border-color: white;
  color: white;
  font-size: 1.2em;
  margin: 1em 0em;
  text-align: center
}

::-webkit-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  font-size: 1em;
}

:-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  font-size: 1em;
}

::-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  font-size: 1em;
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: white;
  font-size: 1em;
}

input:focus::-webkit-input-placeholder {
  opacity: 0;
}




